@import "reset";
@import "layout";

@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC&display=swap");

$primary: #AA0601;
$secondary: #650300;
$pro-color1: #5F3E2D;
$pro-color2: #B75929;

body {
  font-family: "Noto Sans TC", sans-serif;
  line-height: 1.5;
}

*, *::before, *::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #fff;
}

a:hover {
  opacity: 0.5;
}

img {
  width: 100%;
  vertical-align: middle;
}
.bg-pink{
  background-color: #AA06010D;
  color: #650300;
}
.container {
  max-width: 1140px;
  margin: 0 auto;
}

.banner{
  height: 840px;
  background-image: url(/assets/images/banner.png);
  background-size: cover;
  background-position: center;
  right: 0;
 }
 .banner .container{
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
 .content{
  padding-top: 60px;
  padding-bottom: 80px;
}

.banner-info{
   font-size: 36px;
   color: #AA0601;
   text-align: right;
 }
.banner-info h1 span{
  font-size: 64px;
  font-weight: bold;
}
.pro-menu {
  max-width: 1110px;
  margin: 0 auto;
}
.pro-menu ul {
  display: flex;
  font-size: 32px;
}

.pro-menu li {
  width: 33%;
  font-size: 32px;
  border-left: 1px solid #ddd;
  padding: 20px;
  padding-top: 26px;
  display: flex;
  justify-content: center;
  transition: all 0.3s;
  border-bottom: 8px solid #fff;
}
.pro-menu li a {
  display: flex;
  color: #000;
}
.pro-menu li:hover, .pro-menu li.active, .pro-menu li:focus {
  border-bottom: 8px solid #AA0601;
}
.pro-menu li img {
  width: 44px;
  height: 44px;
  margin-right: 8px;
}

.pro-menu li:last-child {
  border-right: 1px solid #ddd;
}

.pro-banners {
  display: flex;
  height: 304px;
}
.pro-banners .pro-banner {
  width: 50%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.pro-banners .pro-banner1 {
  background-image: url(/assets/images/banner-3.png);
}
.pro-banners .pro-banner2 {
  background-image: url(/assets/images/product-2-color.png);
}

.pro-content {
  max-width: 1110px;
  margin: 0 auto;
}
.pro-content h2.title {
  margin-top: 80px;
  text-align: center;
  font-size: 32px;
}
.pro-content h2.title span {
  font-weight: bold;
  font-size: 42px;
}
.pro-content ul {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  margin-top: 40px;
  margin-bottom: 104px;
}
.pro-content ul li {
  margin-right: 30px;
  text-transform: uppercase;
  width: 30%;
  margin-bottom: 104px;
}

.pro-content ul img {
  width: 100%;
}

.pro-inner {
  width: 100%;
  display: flex;
  justify-content: space-between;
  font-size: 24px;
}
.pro-inner .pro-price {
  color: #AA0601;
  font-weight: bold;
}

.pro-color {
  display: flex;
}
.pro-color .pro-color-rect {
  margin-right: 8px;
  width: 24px;
  height: 24px;
  border-radius: 2px;
  background-color: #5F3E2D;
}
.pro-color .pro-color-rect2 {
  background-color: #B75929;
}


.pages {
  display: none;
  margin-bottom: 56px;
}
.pages ul {
  display: flex;
  justify-content: center;
}
.pages ul li a {
  padding: 12px;
  border: 1px solid #ddd;
  color: #000;
  transition: all 0.5s;
}
.pages ul li a:hover {
  background-color: #000;
  color: #fff;
}
.pages ul li a:active {
  background-color: #F2F2F2;
}

@media (max-width: 768px) {
  .container {
    max-width: 720px;
  }

  .pro-menu {
    max-width: 720px;
  }

  .pro-menu li {
    font-size: 24px;
    padding: 14px 20px;
  }
  .pro-menu li img {
    width: 30px;
    height: 30px;
  }

  .pro-content ul li {
    justify-content: space-between;
    width: 47%;
    margin-bottom: 72px;
  }
  .pro-content ul li:nth-child(even) {
    margin-right: 0;
  }
}
@media (max-width: 375px) {
  .container {
    max-width: 345px;
  }

  .header-inner {
    display: flex;
    flex-direction: column;
  }
  .header-inner .logo {
    padding: 10px;
    display: flex;
    justify-content: center;
  }
  .header-inner .logo img {
    width: 48px;
    height: 30px;
  }
  .header-inner ul {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
  }
  .header-inner ul li {
    display: flex;
    justify-content: flex-start;
    font-size: 20px;
    width: 50%;
    border: 1px solid #fff;
    padding: 8px 0;
  }
  .header-inner ul li a {
    margin: 0 auto;
  }

  .pro-menu li {
    border-bottom: 4px solid #fff;
  }
  .pro-menu li:hover, .pro-menu li.active, .pro-menu li:focus {
    border-bottom: 4px solid #AA0601;
  }

  .pro-menu {
    max-width: 345px;
    margin: 0 auto;
  }

  .pro-menu li {
    font-size: 14px;
    padding: 4px;
    display: flex;
    justify-content: center;
  }
  .pro-menu li img {
    width: 15px;
    height: 15px;
    margin-right: 0;
    margin-top: 4px;
  }

  .pro-banners {
    height: 148px;
  }

  .pro-content h2.title {
    font-size: 16px;
    margin: 20px 0;
  }
  .pro-content h2.title span {
    font-weight: bold;
    font-size: 20px;
  }
  .pro-content ul li {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin-right: 0;
    margin-bottom: 32px;
    font-size: 16px;
  }

  .pages {
    display: block;
  }
}


   .icon{
     text-align: center;
   }
   .icon p{
    color: #000;
  }
   .icon ul{
     display: flex;
     justify-content: space-between;
     align-items: baseline;
   }
   .icon li{
     width: 25%;
     margin-left: 28px;
   }
   .icon img{
     max-width: 84px;
     margin-bottom: 24px;
   }
   h2.title{
     padding-top: 60px;
     font-size: 48px;
     text-align: center;
     color: #650300;
     font-weight: bold;
   }
   h3{
    font-size: 24px;
    font-weight: bold;
   }
   .product-list{
     margin-top: 40px;
   }
   .product-list li{
     display: flex;
     align-items: center;
     color: #aa0601;
     margin-bottom: 32px;
   }
   .product-list li:nth-child(2){
    justify-content: space-between;
  }
  .product-list li:nth-child(2) .pro-info{
    margin-right: 0px;
  }
   .product-list h3{
     display: flex;
     justify-content: center;
     align-items: center;
     font-family: Roboto;
     font-size: 48px;
     font-weight: bold;
     text-transform: uppercase;
   }
   .product-list img{
     max-width: 635px;
   }
   .pro-info{
     display: flex;
     justify-content: center;
     margin-left: 32px;
   }
   .products{
     position: relative;
     width: 100%;
     height: 100%;
     background-image: url(/assets/images/bg.png);
     background-size: cover;
     background-position: center;
   }
   .products h2.title{
     color: #fff;
     margin-bottom: 40px;
   }
   .products-list{
     display: flex;
   }

  .products-image{
    height: 455px;
    width: 540px;
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  .products-image1{
    background-image: url(/assets/images/product-8.png);
  }
  .products-image2{
    background-image: url(/assets/images/product-9.png);
  }
  .products-image:after{
    content: "";
    background-color: #fff;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0.7;
  }
  .products-list li{
    margin-left: 30px;
  }
   .products-list li h4{
     display: flex;
     justify-content: center;
     align-items: center;
     font-size: 72px;
     color: #AA0601;
     font-weight: bold;
     z-index: 3;
   }
   .rec-list{
     display: flex;
     justify-content: space-between;
     margin-top: 40px;
   }
   .rec-list li{
     width: 25%;
     height: 460px;
     margin-left: 30px;
     border-radius: 5px;
     box-shadow: 3px 3px 5px #ddd;
   }
   .rec-list li img{
      max-height: 256px;
   }
   .rec-info{
     display: flex;
     justify-content: space-between;
     flex-direction: column;
     padding: 16px;
   }

   .rec-info .date{
     color:#707070;
   }
   .contact-form{
     margin: 0 190px;
     max-width: 730px;
     color: #000;
     margin-top: 20px;
   }
   .contact-form p{
     margin-top: 24px;
     margin-bottom: 20px;
     font-size: 20px;
   }
   .contact-form label,
   .contact-form input{
     display: block;
     width: 100%;
     margin-bottom: 4px;
     background-color: transparent;
    }
   .contact-form input{
     margin-bottom: 16px;
     border: 1px solid #000;
   }
   .contact-form input[type="checkbox"]{
    width: 24px;
    height: 24px;
    border: 2px solid #000;
   }
   .contact-form input[type="submit"]{
    margin: 0 auto;
    width: 350px;
    border: none;
    background-color: $primary;
    color: #fff;
    padding: 16px;
   }
   .privacy-item{
     display: flex;
     margin-bottom: 40px;
   }
   .contact-form input[placeholder]{
    padding: 16px;
   }